<template>
  <div>
    <button
      class="customStartButton"
      v-if="state.profile.isSigned"
      @click="dashboard()"
    >
      {{ $t("base.dashboardText") }}
    </button>
    <button
      class="customStartButton"
      v-if="!state.profile.isSigned"
      @click="startLogin()"
    >
      {{ $t("landingPage.section1.text3") }}
    </button>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import router from "../router/router";
import { setLocale } from "../services/firebase";

export default {
  name: "StartBtn",
  props: [],
  mounted() {
    setLocale();
  },
  methods: {
    dashboard: function () {
      router.push("/admin");
    },
    startLogin: function () {
      this.$store.dispatch("startLogin");
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      state: "state",
    }),
  },
};
</script>
